.oneGame {
    /* border: 2px solid black; */
    /* border-radius: 30px; */
    width: 800px;
    margin: 20px auto;
    position: relative;
    min-height: 400px;
}

.oneGame::after{
    content: '';
    position: absolute;
    inset: 0;
    background-image: url('../images/honky-rink.png');
    background-size: 100% 100%;
    border-radius: 35px;
    height: inherit;
    opacity: .4;
    z-index: -1;
}

.infoFlex {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
    padding-top: 30px;
}

.awayTeamInfo, .homeTeamInfo {
    width: 250px;
    border: 1px solid black;
    background-color: white;
    border-radius: 10px;
}

.awayTeamName, .homeTeamName {
    margin-bottom: 0;
    font-size: 20px;
}

.awayLogo, .homeLogo {
    width: 150px;
}

.awayRecord, .homeRecord {
    margin: 0 0 20px 0;
}

.gameStatus {
    margin: 10px;
    border: 1px solid black;
    background-color: white;
    padding: 5px;
}

.gameProgress {
    margin: 5px;
}

.gameScore {
    font-size: 50px;
    margin: 10px auto;
    border: 1px solid black;
    background-color: white;
    border-radius: 20px;
    padding: 5px 15px;
}

.oneTeamGoals {
    margin: 20px 0;
    width: 240px;
}

.oneGoal {
    margin: 2px;
    font-size: 14px;
    height: 15px;
    padding-bottom: 5px;
}

.gameGoals {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    gap: 10px;
}

hr {
    margin: 10px;
    color: white;
    opacity: 0;
}

.player-goal-background {
    background-color: white;
    border: 1px solid black;
    border-radius: 20px;
}

@media (max-width: 800px) {
    .infoFlex {
        padding-top: 20px;
    }
    .oneGame {
        width: 600px;
        min-height: 300px;
    }
    .awayTeamInfo, .homeTeamInfo {
        width: 100px;
        border: 1px solid black;
    }
    .awayLogo, .homeLogo {
        width: 100px;
    }
    .awayTeamInfo, .homeTeamInfo {
        width: 200px;
    }
    
    .awayTeamName, .homeTeamName {
        margin-bottom: 0;
        font-size: 10px;
    }

    .gameStatus {
        margin: 5px;
    }
    
    .gameProgress {
        margin: 0;
    }

    .gameScore {
        font-size: 30px;
    }

    .oneTeamGoals {
        margin: 0px 0;
        width: 240px;
    }
    
    .oneGoal {
        margin: 2px;
        margin-bottom: 1px;
        font-size: 12px;
        height: 18px;
        padding-bottom: 0px;
    }

    .player-goal-background {
        border: 1px solid black;
    }

    .awayRecord {
        font-size: 10px;
        margin-bottom: 5px;
    }

    .homeRecord {
        font-size: 10px;
        margin-bottom: 5px;
    }
    
  }

  @media (max-width: 500px) {
    .infoFlex {
        padding-top: 3px;
    }
    .oneGame {
        width: 325px;
        min-height: 160px;
    }
    .awayTeamInfo, .homeTeamInfo {
        width: 100px;
        border: 1px solid black;
    }
    .awayLogo, .homeLogo {
        width: 50px;
    }
    .awayTeamInfo, .homeTeamInfo {
        width: 100px;
    }
    
    .awayTeamName, .homeTeamName {
        margin-bottom: 0;
        font-size: 10px;
    }

    .gameStatus {
        margin: 5px;
    }
    
    .gameProgress {
        margin: 0;
    }

    .gameScore {
        font-size: 20px;
    }

    .oneTeamGoals {
        margin: 0px 0;
        width: 240px;
    }
    
    .oneGoal {
        margin: 0;
        margin-bottom: 1px;
        font-size: 8px;
        height: 12px;
        padding-bottom: 0px;
    }

    .player-goal-background {
        border: 1px solid black;
    }

    .awayRecord {
        font-size: 10px;
        margin-bottom: 5px;
    }

    .homeRecord {
        font-size: 10px;
        margin-bottom: 5px;
    }
    
  }